.background {
    width: 100%;
}

.avatar {
    width: 200px;
    margin-bottom: 10px;
}

.contact {
    padding-left: 15px;
    padding-bottom: 2px;
}

input[type = 'file'] {
    position: absolute;
    opacity: 0;
    visibility: hidden;
}

label {
    position: absolute;
    color: white;
    left: 574px;
    top: 69px;
    font-size: 16px;
    background-color: #5181b8;
    padding: .3em .6em;
    cursor: pointer;
    transition: box-shadow .4s ease;
}

label:hover {
    box-shadow: 0 0 15px rgba(27, 112, 168, 0.5);
}